.tracking-in-expand {
  text-align: center;
  animation: tracking-in-expand .7s cubic-bezier(.215, .61, .355, 1) both;
}

@keyframes tracking-in-expand {
  0% {
    letter-spacing: -.5em;
    opacity: 0;
  }

  40% {
    opacity: .6;
  }

  100% {
    opacity: 1;
  }
}

.breed-select {
  cursor: pointer;
  color: #522344;
  width: 100%;
  background-color: #d7c2ec;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 10px;
  font-size: 14px;
  font-weight: 700;
  display: block;
}

.color-change-2x {
  animation: color-change-2x 2s linear infinite alternate both;
}

@keyframes color-change-2x {
  0% {
    background: #19dcea;
  }

  100% {
    background: #b22cff;
  }
}

